.mainheader {
  width: 100vw;
  overflow: hidden;
  height: 10vh;
  -webkit-box-shadow: 2px 4px 2px #999999;
          box-shadow: 2px 4px 2px #999999;
}
.mainheader .logo {
  width: 20vw;
  height: 10vh;
  float: left;
  font-size: 1rem;
}
.mainheader p {
  float: left;
  width: 60vw;
  font-size: 2rem;
  font-weight: 600;
  height: 10vh;
  line-height: 10vh;
  text-align: center;
}
.mainheader .tips {
  float: left;
  width: 5vw;
  height: 10vh;
  line-height: 10vh;
}
.mainheader .tips a {
  color: #80858f;
  font-weight: 600;
}
.mainheader .userInfo {
  float: left;
  width: 10vw;
  height: 10vh;
  line-height: 10vh;
}
.mainheader .setup {
  float: left;
  width: 5vw;
  font-size: 2rem;
  color: #80858f;
  height: 10vh;
  line-height: 10vh;
}
.mainheader .setup a {
  color: #80858f;
}

.mainleft {
  width: 15vw;
  height: 90vh;
  background: #495060;
  overflow-y: auto;
}
.mainleft .ivu-menu-dark {
  width: 100%!important;
}

.mainfooter {
  width: 85vw;
  height: 8vh;
  position: absolute;
  bottom: 0;
  right: 0;
  border-top: 1px solid #1a1a1a;
  padding-top: 1vh;
  font-weight: 600;
}
.mainfooter p {
  color: #1b6d85;
  text-align: center;
}
.mainfooter p a {
  color: #1b6d85;
  -webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;
}
.mainfooter p a:hover {
  color: #ff6600;
}

.MainContent {
  width: 85vw;
  height: 80vh;
  background: #ffffff;
  position: absolute;
  top: 11vh;
  right: 0;
}
.MainContent .MianPage {
  margin: 2vh 2vw;
}

.Login .login_model {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.7);
}
.Login .login_form {
  width: 36vw;
  height: 40vh;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  z-index: 99;
  background: rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: 0 0 0 10px rgba(0, 0, 0, 0.8);
          box-shadow: 0 0 0 10px rgba(0, 0, 0, 0.8);
  border: 4px solid rgba(50, 50, 55, 0.2);
  border-radius: 5px;
  text-align: center;
}
.Login .login_form .login_logo {
  overflow: hidden;
}
.Login .login_form .login_logo a {
  display: inline-block;
  float: left;
  width: 5rem;
  height: 5rem;
  margin: 0.5rem 2.5rem;
  background: url(./dist/static/images/wlxs_logo.9ed5fc96.png) no-repeat center center;
  background-size: cover;
}
.Login .login_form .login_logo span {
  display: inline-block;
  float: left;
  color: rgba(255, 255, 255, 0.5);
  font-size: 2.5rem;
  margin: 2vh;
}
.Login .login_form .username {
  margin-top: 3vh;
  color: #fff;
  font-size: 1rem;
}
.Login .login_form .username input {
  background: #1B1B1B;
  outline-color: #00ff00;
  border: none;
  border-radius: 3px;
  color: #fff;
  height: 2rem;
  text-align: center;
}
.Login .login_form .password {
  color: #fff;
  font-size: 1rem;
}
.Login .login_form .password input {
  background: #1B1B1B;
  border: none;
  outline-color: #00ff00;
  border-radius: 3px;
  color: #fff;
  height: 2rem;
  margin-top: 2vh;
  text-align: center;
}
.Login .login_form .remember {
  margin-top: 10px;
  color: #fff;
  height: 0.8rem;
}
.Login .login_form .remember span:nth-of-type(2),
.Login .login_form .remember span:nth-of-type(3) {
  display: inline-block;
  width: 8rem;
}
.Login .login_form .remember .tipTitle {
  text-align: right;
  color: #ff6600;
  font-weight: 600;
}
.Login .login_form .form_submit input {
  background: #1B1B1B;
  border: 1px solid #555;
  outline: medium;
  border-radius: 3px;
  color: #fff;
  width: 10rem;
  height: 2rem;
  margin-top: 2vh;
  cursor: pointer;
  -webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;
}
.Login .login_form .form_submit input:hover {
  background: #2779aa;
  color: #000;
}
.Login .login_bg {
  width: 100vw;
  height: 100vh;
  background: url(./dist/static/images/login_bg.2aa529c0.png) no-repeat center center;
  background-size: cover;
}
.Login footer {
  position: absolute;
  width: 100vw;
  bottom: 3vh;
  color: #fff;
}
.Login footer p {
  text-align: center;
}
@media screen and (max-width: 900px) {
.Login .login_form {
    width: 90%;
    height: 50vh;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    z-index: 99;
    background: rgba(0, 0, 0, 0.75);
    -webkit-box-shadow: 0 0 0 10px rgba(0, 0, 0, 0.8);
            box-shadow: 0 0 0 10px rgba(0, 0, 0, 0.8);
    border: 4px solid rgba(50, 50, 55, 0.2);
    border-radius: 5px;
    text-align: center;
}
.Login .login_form .login_logo {
    overflow: hidden;
}
.Login .login_form .login_logo a {
    display: block;
    width: 15rem;
    height: 4rem;
    margin: 0.5rem 2.5rem;
    background: url(./dist/static/images/wlxs_logo.9ed5fc96.png) no-repeat center center;
    background-size: cover;
}
.Login .login_form .login_logo span {
    display: block;
    color: rgba(255, 255, 255, 0.5);
    font-size: 1.5rem;
    margin: 1vh;
    width: 100%;
    text-align: center;
}
}

.Jurisdic {
  font-family: Arial, sans-serif;
}
.Jurisdic .userListHeader {
  background: rgba(33, 112, 147, 0.5);
  list-style: none;
  overflow: hidden;
}
.Jurisdic .userListHeader li {
  padding: 1vh 0;
  width: 12vw;
  float: left;
  border-left: 1px solid #e9eaec;
  color: #333;
  font-weight: 600;
  text-align: center;
}
.Jurisdic .userListHeader li:nth-of-type(1) {
  width: 5vw;
}
.Jurisdic .userListHeader li:nth-last-of-type(1) {
  width: 28vw;
}
.Jurisdic .userList {
  background: #fff;
  list-style: none;
  height: 70vh;
  padding: 2vh 0;
  overflow: auto;
}
.Jurisdic .userList > li {
  border: 1px solid #e9eaec;
  margin: 0;
  -webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;
}
.Jurisdic .userList > li:nth-of-type(1) {
  margin: 0;
}
.Jurisdic .userList > li:hover {
  background: rgba(154, 207, 234, 0.5);
}
.Jurisdic .userList > li .userItem {
  overflow: hidden;
  list-style: none;
}
.Jurisdic .userList > li .userItem li {
  padding: 1vh ;
  width: 12vw;
  float: left;
  border-left: 1px solid #e9eaec;
}
.Jurisdic .userList > li .userItem li:nth-of-type(1) {
  width: 5vw;
  text-align: center;
}
.Jurisdic .userList > li .userItem li:nth-last-of-type(1) {
  width: 28vw;
}
.Jurisdic .opration {
  text-align: center;
  padding-top: 10px;
}

.NoticeList {
  padding: 2vh;
  height: 80vh;
  overflow-y: auto;
}
.NoticeList header {
  font-size: 2.5vh;
  font-weight: 600;
}

